<!--  -->
<template>
    <div class="clearfix">
        <form :class="['form-horizontal', 'clearfix', 'form-search', {'form-min': isModal}]" name="serverForm" method="POST">
            <div class="col-sm-3">
                <div class="form-group">
                    <label class="control-label col-sm-4">SN</label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" placeholder="" onfocus="this.blur()" v-model="param.sn" @click="showBroad('param.sn')">
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" placeholder="" onfocus="this.blur()" v-model="param.serverNum" @click="showBroad('param.serverNum')">
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="param.room" :options="rooms" placeholder="" :search="true">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control time-input fs12" onfocus="this.blur()" @click="showCalendar('show1', $event)" v-model="param.addTime" placeholder="">
                        <calendar :show.sync="show1" :value.sync="param.addTime" :x="x" :y="y" :range="range" :type="type"></calendar>
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="param.product" :options="products" placeholder="" :search="true" :disabled="isModal">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" v-model="param.maintainManager">
                    </div>
                </div>
            </div>
            <div class="col-sm-3">
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" placeholder="" onfocus="this.blur()" v-model="param.assetNum" @click="showBroad('param.assetNum')">
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="param.serverType" :options="serverTypes" placeholder="">
                        </v-select>
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="param.frame" :options="frames" placeholder="" :search="true">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control time-input fs12" onfocus="this.blur()" @click="showCalendar('show3', $event)" v-model="param.factoryTime" placeholder="">
                        <calendar :show.sync="show3" :value.sync="param.factoryTime" :x="x" :y="y" :range="range" :type="type"></calendar>
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-4 input-box pr0">
                        <v-select :value.sync="param.department1" :options="departments1" placeholder="">
                        </v-select>
                    </div>
                    <div class="col-sm-4 input-box pl0">
                        <v-select :value.sync="param.department2" :options="departments2" placeholder="">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4">IP</label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" placeholder="" onfocus="this.blur()" v-model="param.ip" @click="showBroad('param.ip')">
                    </div>
                </div>
            </div>
            <div class="col-sm-3 input-box">
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" placeholder="" onfocus="this.blur()" v-model="param.financeNum" @click="showBroad('param.financeNum')">
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="param.system" :options="systems" placeholder="">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-4 input-box pr0">
                        <v-select :value.sync="param.origin1" :options="origins1" placeholder="">
                        </v-select>
                    </div>
                    <div class="col-sm-4 input-box pl0">
                        <v-select :value.sync="param.origin2" :options="origins2" placeholder="">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control time-input fs12" onfocus="this.blur()" @click="showCalendar('show2', $event)" v-model="param.procureTime" placeholder="">
                        <calendar :show.sync="show2" :value.sync="param.procureTime" :x="x" :y="y" :range="range" :type="type"></calendar>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4">Set</label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" v-model="param.set">
                    </div>
                </div>
            </div>
            <div class="col-sm-3">
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" placeholder="" onfocus="this.blur()" v-model="param.invoiceNum" @click="showBroad('param.invoiceNum')">
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="param.status" :options="statusArr" placeholder="">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" placeholder="" v-model="param.model">
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-4"></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="param.firm" :options="firms" placeholder="">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-4">Module</label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" v-model="param.module">
                    </div>
                </div>
            </div>
        </form>
        <div class="text-center btn-operate">
            <button type="button" class="btn btn-default" @click="refresh">
                
            </button>
            <span v-if="!isModal">
                <button v-if="perm. || perm.all" type="button" class="btn btn-default" @click="$broadcast('showCreateServer')">
                    
                </button>
                <button v-if="perm. || perm.all" type="button" class="btn btn-default" @click="dispatchFn">
                    
                </button>
                <button v-if="perm. || perm.all" type="button" class="btn btn-default" @click="batchEdit">
                    
                </button>
                <dropdown v-el:deleteconfirm v-if="perm. || perm.all">
                    <button type="button" class="btn btn-default" data-toggle="dropdown">
                        
                        <span class="caret"></span>
                    </button>
                    <div slot="dropdown-menu" class="dropdown-menu pd20">
                        <button type="button" class="btn btn-danger btn-block" @click="checkedFn('/device/server/delete/', 'deleteconfirm')"></button>
                        <button type="button" class="btn btn-default btn-block" @click="cancelFn('deleteconfirm')"></button>
                    </div>
                </dropdown>
                <button v-if="perm. || perm.all" type="button" class="btn btn-default" @click="exportFn">
                    
                </button>
                <dropdown v-el:recoverconfirm v-if="perm. || perm.all">
                    <button type="button" class="btn btn-default" data-toggle="dropdown">
                        
                        <span class="caret"></span>
                    </button>
                    <div slot="dropdown-menu" class="dropdown-menu pd20">
                        <button type="button" class="btn btn-danger btn-block" @click="checkedFn('/node/recover/', 'recoverconfirm')"></button>
                        <button type="button" class="btn btn-default btn-block" @click="cancelFn('recoverconfirm')"></button>
                    </div>
                </dropdown>
                <dropdown v-el:backconfirm v-if="perm['IDC'] || perm.all">
                    <button type="button" class="btn btn-default" data-toggle="dropdown">
                        IDC
                        <span class="caret"></span>
                    </button>
                    <div slot="dropdown-menu" class="dropdown-menu pd20">
                        <button type="button" class="btn btn-danger btn-block" @click="checkedFn('/node/backIDC/', 'backconfirm')"></button>
                        <button type="button" class="btn btn-default btn-block" @click="cancelFn('backconfirm')"></button>
                    </div>
                </dropdown>
            </span>
            <span v-if="isModal">
                <button type="button" class="btn btn-default" @click="getChecked">
                    
                </button>
            </span>
        </div>
        <div class="text-center table-title">
            
            <div class="pull-left">
                <dropdown>
                    <button type="button" class="btn btn-default set-btn" data-toggle="dropdown">
                        <span class="glyphicon glyphicon-cog"></span>
                    </button>
                    <div slot="dropdown-menu" class="dropdown-menu dropdown-width">
                        <ul class="pull-left dropdown-width">
                            <li v-for="check in checkArr" class="pull-left dropdown-li" track-by="$index">
                                <input :id="'fliter' + $index" type="checkbox" :checked="check.checked" @click="fliter($index)"> 
                                <label :for="'fliter' + $index" v-text="check.label"></label>
                            </li>
                        </ul>
                    </div>
                </dropdown>
            </div>
        </div>
        <div class="table-box">
            <div class="table-wrapper">
                <table :class="['table', 'table-hover', 'table-bordered', {'table-small': isModal}]">
                    <thead>
                        <tr>
                            <th width="2%"><input type="checkbox" v-model="checkedAll"></th>
                            <th v-for="title in titles" v-text="title"></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="list in tableList" v-if="tableList.length !== 0" v-show="tableList.length !== 0">
                            <td><input type="checkbox" :id="list.id" :value="list.id" v-model="checkedIds"></td>
                            <td v-for="value in valueArr" v-if="value === 'serverNum'">
                                <a class="pointer" v-if="value === 'serverNum'" v-text="list[value]" @click="$broadcast('showEditServer', list.id)"></a>
                            </td>
                            <td v-for="value in valueArr" :title="list[value]" v-text="list[value]" v-if="value !== 'serverNum'">
                            </td>
                        </tr>
                        <tr class="text-center" v-show="tableList.length === 0">
                            <td :colspan="titles.length + 1"></td>
                        </tr>
                    </tbody>
                </table>
                <spinner id="spinner-box" :size="md" :fixed="false" 
                     text="..." v-ref:spinner>
                </spinner>
            </div>
        </div>
        <div class="pull-right mt30">
            <boot-page v-ref:page :async="true" :lens="lenArr" :page-len="pageLen" :url="url" :param="param"></boot-page>
        </div>

        <create-server-modal></create-server-modal>
        <batch-edit-modal></batch-edit-modal>
        <edit-server-modal></edit-server-modal>
        <dispatch-modal></dispatch-modal>
    </div>
</template>

<script>
import { dropdown, spinner } from 'vue-strap'
import bootPage from '../../global/BootPage.vue'
import createServerModal from './createServer.vue'
import batchEditModal from './BatchEdit.vue'
import editServerModal from './EditServer.vue'
import dispatchModal from './Dispatch.vue'
import vSelect from '../../global/Select.vue'
import calendar from '../../global/Calendar.vue'
import { getServerSearch, getFramesSeats, getOrigins } from '../../../vuex/action.js'
import { idcs, frames, products, serverTypes, departments1, systems, serverStatus, firms, origins1, origins2, perm } from '../../../vuex/getters.js'

export default {
    data () {
        return {
            isModal: false,
            checkedAll: false,
            checkedIds: [],
            titles: [],
            tableList: [],
            lenArr: [10, 50, 100],
            pageLen: 5,
            url: '/device/server/query/',
            departments2: [],
            param: {
                sn: '',
                serverNum: '',
                assetNum: '',
                financeNum: '',
                invoiceNum: '',
                room: '',
                frame: '',
                ip: '',
                firm: '',
                serverType: '',
                status: '',
                model: '',
                system: '',
                origin1: '',
                origin2: '',
                addTime: '',
                factoryTime: '',
                procureTime: '',
                department: '',
                product: '',
                maintainManager: '',
                module: '',
                set: '',
                department1: '',
                department2: ''
            },
            checkArr: [
                {label: 'SN', value: 'sn', checked: true},
                {label: '', value: 'serverType', checked: true},
                {label: '', value: 'origin', checked: true},
                {label: '', value: 'status', checked: true},
                {label: '', value: 'companyIntnet', checked: true},
                {label: '', value: 'roomIntnet', checked: true},
                {label: '', value: 'roomOutnet', checked: true},
                {label: '', value: 'product', checked: true},
                {label: 'set', value: 'set', checked: true},
                {label: 'module', value: 'module', checked: true},
                {label: '', value: 'maintainManager', checked: true},
                {label: '', value: 'system', checked: true},
                {label: 'IP', value: 'ip', checked: false},
                {label: '', value: 'model', checked: false},
                {label: '', value: 'firm', checked: false},
                {label: '', value: 'room', checked: true},
                {label: '', value: 'frame', checked: true},
                {label: '', value: 'seat', checked: true},        
                {label: '', value: 'addTime', checked: false},
                {label: '', value: 'factoryTime', checked: false},
                {label: '', value: 'procureTime', checked: false},
                {label: '', value: 'assetNum', checked: false},
                {label: '', value: 'financeNum', checked: false},
                {label: '', value: 'invoiceNum', checked: false},
                {label: '', value: 'shelfLife', checked: false},
                {label: 'CPU', value: 'cpu', checked: false},
                {label: '', value: 'mem', checked: false},
                {label: '', value: 'hostNum', checked: false},
                {label: '', value: 'serverUseType', checked: false},
                {label: '', value: 'usage', checked: false},
                {label: '', value: 'serverUseProduct', checked: false},
                {label: '', value: 'catcher', checked: false},
                {label: '', value: 'price', checked: false},
                {label: '', value: 'remark', checked: true}
            ],
            valueArr: [],
            show1: false,
            show2: false,
            show3: false,
            type: 'date', 
            x: 0,
            y: 0,
            range: true
        }
    },
    methods: {

        // 
        refresh () {
            this.$refs.spinner.show()
            this.checkedIds = []
            this.$refs.page.refresh()
        },

        // 
        fliter (index) {
            this.checkArr[index].checked ? this.checkArr[index].checked = false : this.checkArr[index].checked = true

            this.originFilter()
        },

        // 
        originFilter () {
            let _this = this

            this.titles = []
            this.valueArr = []

            this.checkArr.forEach((e) => {
                if (e.checked) {
                    _this.titles.push(e.label)
                    _this.valueArr.push(e.value)
                }
            })

            this.titles.unshift('')
            this.valueArr.unshift('serverNum')
        },

        // 
        batchEdit () {
            let _this = this

            if (this.checkedIds.length) {
                this.$broadcast('batchEdit', _this.checkedIds)
            } else {
                this.$dispatch('show-notify', '')
            }
        },

        // 
        dispatchFn () {
            let _this = this

            if (this.checkedIds.length) {
                this.$broadcast('showDispatchModal', _this.checkedIds)
            } else {
                this.$dispatch('show-notify', '')
            }
        },

        // 
        showBroad (target) {
            let obj = target.split('.')

            let param = {
                value: this[obj[0]][obj[1]],
                name: target
            }

            this.$dispatch('showBroad', param)
        },

        // 
        showCalendar (name, e) {
            e.stopPropagation();

            var that = this;
            that[name] = true;
            that.x = e.target.offsetLeft;
            that.y = e.target.offsetTop + e.target.offsetHeight + 8;

            var bindHide = function(e) {
                e.stopPropagation();
                that[name] = false;
                document.removeEventListener('click', bindHide, false);
            };

            setTimeout(function() {
                document.addEventListener('click', bindHide, false);
            }, 500);
        },

        // 
        exportFn () {
            let form = document.serverForm,
                arr = []

            for (let key in this.$data.param) {
                let obj = key + '=' + this.$data.param[key]

                arr.push(obj)
            }

            form.action='/device/server/export/?' + arr.join('&')

            form.submit()
        },

        // 
        getChecked () {
            let _this = this

            if (this.checkedIds.length) {
                this.$dispatch('getServerData', _this.checkedIds)
            } else {
                this.$dispatch('show-notify', '')
            }
        },

        // IDC
        checkedFn (url, name) {
            if (this.checkedIds.length) {
                this.$http({
                    url: url,
                    method: 'POST',
                    data: {
                        checkedIds: this.checkedIds
                    }
                })
                .then(response => {
                    if (response.data.code === 200) {
                        this.checkedIds = []
                        this.refresh()

                        this.$dispatch('show-success')
                    } else {
                        this.$dispatch('show-error', response.data.msg)
                    }
                })
            } else {
                this.$dispatch('show-notify', '')
            }

            this.$els[name].classList.toggle('open')
        },

        // confirm
        cancelFn (name) {
            this.$els[name].classList.toggle('open')
        }
    },
    components: {
        bootPage,
        createServerModal,
        batchEditModal,
        editServerModal,
        vSelect,
        calendar,
        dropdown,
        spinner,
        dispatchModal
    },
    vuex: {
        actions: {
            getServerSearch,
            getFramesSeats,
            getOrigins
        },
        getters: {
            perm,
            rooms: idcs,
            frames,
            products,
            serverTypes,
            departments1,
            systems,
            origins1,
            origins2,
            statusArr: serverStatus,
            firms
        }
    },
    ready () {
        this.getServerSearch()
        this.originFilter()
        this.$refs.spinner.show()
    },
    watch: {
        'checkedAll' (newVal) {
            if (newVal) {
                if (this.checkedIds.length !== this.tableList.length) {
                    let _this = this

                    _this.checkedIds = []
                    _this.tableList.forEach(function(e) {
                        _this.checkedIds.push(e.id)
                    })
                }
            } else {
                if (this.checkedIds.length === this.tableList.length) {
                    this.checkedIds = []
                }
            }
        },
        'checkedIds' (newVal) {
            if (newVal.length === this.tableList.length && this.tableList.length !== 0) {
                this.checkedAll = true
            } else {
                this.checkedAll = false
            }
        },
        'param.room' (newVal) {
            this.param.frame = ''

            this.getFramesSeats(newVal, 'room')
        },
        'param.origin1' (newVal) {
            this.param.origin2 = ''

            this.getOrigins(newVal)
        },
        'param.department1' (newVal) {
            this.param.department2 = ''

            this.$http({
                url: '/product/getDepartment/?id=' + newVal,
                method: 'GET'
            })
            .then(response => {
                this.departments2 = response.data.departments2
            })
        }
    },
    events: {

        // 
        'data' (param) {
            this.tableList = param.data
            this.checkedIds = []
            this.$refs.spinner.hide()
        },

        // 
        'refresh' () {
            this.refresh()
        },

        // 
        'getTxt' (param) {
            let obj = param.name.split('.')

            this[obj[0]][obj[1]] = param.val
        },

        // 
        'showServerModal' (param) {
            this.isModal = true
            this.param.product = param
            this.refresh()
        }
    }
}
</script>

<style scoped>
.dropdown-width {
    width: 500px;
}

.dropdown-li {
    width: 50%;
}

.pd20 {
    padding: 20px;
}
</style>



