<!--  -->
<template>
    <modal :show.sync="creatServerModal" effect="fade" width="1200px">
        <div slot="modal-header" class="modal-header">
            <h4 class="modal-title"></h4>
        </div>
        <div slot="modal-body" class="modal-body min-height">
            <form class="form-horizontal clearfix form-input">
                <div class="col-sm-6">
                    <div class="form-group">
                        <label class="control-label col-sm-4">SN<span class="text-danger">*</span></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="sn">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"><span class="text-danger">*</span></label>
                        <div class="col-sm-4 input-box pr0">
                            <v-select :value.sync="origin1" :options="origins1" placeholder="">
                            </v-select>
                        </div>
                        <div class="col-sm-4 input-box pl0">
                            <v-select :value.sync="origin2" :options="origins2" placeholder="">
                            </v-select>
                        </div>
                    </div>
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"><span class="text-danger">*</span></label>
                        <div class="col-sm-8">
                            <v-select :value.sync="room" :options="rooms" placeholder="">
                            </v-select>
                        </div>
                    </div>
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"><span class="text-danger">*</span></label>
                        <div class="col-sm-8">
                            <v-select :value.sync="frame" :options="frames" placeholder="">
                            </v-select>
                        </div>
                    </div>
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"><span class="text-danger">*</span></label>
                        <div class="col-sm-8">
                            <v-select :value.sync="seat" :options="seats" placeholder="">
                            </v-select>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <datepicker
                              :value.sync="addTime"
                              :format="'yyyy-MM-dd'"
                              :show-reset-button="true">
                            </datepicker>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <datepicker
                              :value.sync="factoryTime"
                              :format="'yyyy-MM-dd'"
                              :show-reset-button="true">
                            </datepicker>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <datepicker
                              :value.sync="procureTime"
                              :format="'yyyy-MM-dd'"
                              :show-reset-button="true">
                            </datepicker>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"><span class="text-danger">*</span></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="model">
                        </div>
                    </div>
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"><span class="text-danger">*</span></label>
                        <div class="col-sm-8">
                            <v-select :value.sync="status" :options="statusArr" placeholder="">
                            </v-select>
                        </div>
                    </div>
                    <div class="form-group input-box min-dropdown">
                        <label class="control-label col-sm-4"><span class="text-danger">*</span></label>
                        <div class="col-sm-8">
                            <v-select :value.sync="firm" :options="firms" placeholder="">
                            </v-select>
                        </div>
                    </div>
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"><span class="text-danger">*</span></label>
                        <div class="col-sm-8">
                            <v-select :value.sync="serverUseType" :options="serverUseTypes" placeholder="">
                            </v-select>
                        </div>
                    </div>
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="usage">
                        </div>
                    </div>
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"><span class="text-danger">*</span></label>
                        <div class="col-sm-8">
                            <v-select :value.sync="serverType" :options="serverTypes" placeholder="">
                            </v-select>
                        </div>
                    </div>
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="catcher">
                        </div>
                    </div>
                </div>
                <div class="col-sm-6">
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <v-select :value.sync="serverUseProduct" :options="products" placeholder="" multiple search>
                            </v-select>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="hostNum">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="assetNum">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="financeNum">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="invoiceNum">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="voltage">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="electric">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="power">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <datepicker
                              :value.sync="shelfLife"
                              :format="'yyyy-MM-dd'"
                              :show-reset-button="true">
                            </datepicker>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"><span class="text-danger">()</span></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="companyIntnet" onfocus="this.blur()" @click="showBroad('companyIntnet')">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"><span class="text-danger">()</span></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="roomIntnet" onfocus="this.blur()" @click="showBroad('roomIntnet')">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="roomOutnet" onfocus="this.blur()" @click="showBroad('roomOutnet')">
                        </div>
                    </div>
                    <div class="form-group input-box">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="price">
                        </div>
                    </div> 
                    <div class="form-group">
                        <label class="control-label col-sm-4"></label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" v-model="remark">
                        </div>
                    </div>
                </div>
            </form>
        </div>
        <div slot="modal-footer" class="modal-footer">
            <button 
                type="button" 
                class="btn btn-default" 
                @click="saveFn"
                :disabled="sn.trim() && origin1 && origin2 && room && frame && seat && model && status && serverType && firm && serverUseType && (companyIntnet.trim() || roomIntnet.trim()) ? false : true"
            >
                
            </button>
            <button type="button" class="btn btn-default" @click='creatServerModal = false'></button>
        </div>
    </modal>
</template>

<script>
import { modal } from 'vue-strap'
import datepicker from '../../global/Datepicker.vue'
import vSelect from '../../global/Select.vue'
import { getFramesSeats, getOrigins } from '../../../vuex/action.js'
import { idcs, frames, seats, serverTypes, addStatusArr, firms, origins1, origins2, serverUseTypes, products } from '../../../vuex/getters.js'

let origin = {
        creatServerModal: false,
        autoId: null,
        sn: '',
        room: '',
        frame: '',
        seat: '',
        origin1: '',
        origin2: '',
        firm: '',
        status: '',
        addTime: '',
        factoryTime: '',
        procureTime: '',
        model: '',
        shelfLife: '',
        serverType: '',
        hostNum: '',
        assetNum: '',
        financeNum: '',
        invoiceNum: '',
        voltage: '',
        electric: '',
        power: '',
        companyIntnet: '',
        roomIntnet: '',
        roomOutnet: '',
        remark: '',
        serverUseType: '-1',
        serverUseProduct: [],
        usage: '',
        catcher: '',
        price: ''
    },
    init = Object.assign({}, origin);

export default {
    data () {
        return origin
    },
    methods: {

        // 
        saveFn () {
            this.$http({
                url: '/device/server/add/',
                method: 'POST',
                data: this.$data
            })
            .then(response => {
                if (response.data.code === 200) {
                    this.creatServerModal = false
                    this.$data = Object.assign({}, init)

                    this.$dispatch('refresh')
                    this.$dispatch('show-success')
                } else {
                    this.$dispatch('show-error', response.data.msg)
                }
            })
        },

        // 
        showBroad (target) {
            let param = {
                value: this[target],
                name: target,
                types: 1
            }

            this.$dispatch('showBroad', param)
        } 
    },
    components: {
        modal,
        vSelect,
        datepicker
    },
    vuex: {
        actions: {
            getFramesSeats,
            getOrigins
        },
        getters: {
            rooms: idcs,
            frames,
            seats,
            origins1,
            origins2,
            serverTypes,
            statusArr: addStatusArr,
            firms,
            serverUseTypes,
            products
        }
    },
    ready () {

    },
    events: {
        'showCreateServer' () {
            this.creatServerModal = true
        },
        'getTxt2' (param) {
            let obj = param.name.split('.')

            this[obj[0]] = param.val
        },
        'showCreateAuto' (param) {
            this.$http({
                url: '/device/server/auto_list/detail/',
                method: 'POST',
                data: {
                    id: param
                }
            })
            .then(response => {
                if (response.data.code === 200) {
                    this.$data = Object.assign({}, origin, response.data)

                    this.autoId = param
                    this.creatServerModal = true
                } else {
                    this.$dispatch('show-error')
                }
            })
        }
    },
    watch: {
        'room' (newVal) {
            this.frame = ''
            this.seat = ''

            this.getFramesSeats(newVal, 'room')
        },
        'frame' (newVal) {
            this.seat = ''

            this.getFramesSeats(newVal, 'shelf')
        },
        'origin1' (newVal) {
            this.origin2 = ''

            this.getOrigins(newVal)
        }
    }
}
</script>

<style scoped>
.min-height {
    min-height: 550px;
}
</style>


